import platform from '../images/platform.png'
import hills from '../images/hills.png'
import background from '../images/background.png'
import splatform from '../images/platformSmallTall.png'
import flag from '../images/flag.png'


import spriteRunLeft from '../images/spriteRunLeft.png'
import spriteRunRight from '../images/spriteRunRight.png'
import spriteStandLeft from '../images/spriteStandLeft.png'
import spriteStandRight from '../images/spriteStandRight.png'

const canvas = document.querySelector('canvas')
const c = canvas.getContext("2d")
canvas.width = 1024
canvas.height = 576






class Player {

  constructor() {
    this.position = { x: 100, y: 80 }
    this.speed = 10
    this.width = 66
    this.height = 150
    this.velocity = { x: 0, y: 1 }
    this.gravity = 0.5
    this.frames = 0
    this.image = createImage(spriteStandRight)
    this.sprites = {
stand:{
  right:createImage(spriteStandRight),
  left:createImage(spriteStandLeft),
  cropWidth:177,
  width: 66
} ,
run:{
  right:createImage(spriteRunRight),
  left:createImage(spriteRunLeft),
  cropWidth:341,
  width:127.875
} ,
    }

     this.currentSprite = this.sprites.stand.right
     this.currentCropWidth = 177
  }


  draw() {
    c.drawImage( 
      this.currentSprite, 
      this.currentCropWidth * this.frames,
      0,
      this.currentCropWidth,
      400,
      this.position.x, this.position.y,this.width,this.height  )
    }

  update() {
this.frames++
if(this.frames > 59 && (this.currentSprite === this.sprites.stand.right || this.currentSprite === this.sprites.stand.left) )
       {this.frames =0}
else if(this.frames > 30 && (this.currentSprite === this.sprites.run.right || this.currentSprite === this.sprites.run.left) )
       {this.frames =0}

    this.draw()
    this.position.y += this.velocity.y
    this.position.x += this.velocity.x

    if (this.position.y + this.height + this.velocity.y <= canvas.height) { this.velocity.y += this.gravity }
  }
}





class Platform {
  constructor({x,y, image}) {
    this.position = { x, y }
     this.image=image
    this.height = this.image.height
    this.width = this.image.width
  }
  draw() {
   c.drawImage( this.image, this.position.x, this.position.y )
  }
}




class GenericObject {
  constructor({x,y, image}) {
    this.position = { x, y }
     this.image=image
    this.height = this.image.height
    this.width = this.image.width
  }
  draw() {
   c.drawImage( this.image, this.position.x, this.position.y )
  }
}




function createImage(imgsrc){
  const image =new Image()
  image.src = imgsrc
return image
}



function init(){

player = new Player()
platformimage = createImage(platform)
splatformimage = createImage(splatform)
platforms = [
  new Platform({x:-1    ,y:470,image:platformimage }),
  new Platform({x:platformimage.width  + 100,y:200,image:platformimage }),
  new Platform({x:platformimage.width *2 + 200 ,y:400,image:platformimage }),
  new Platform({x:platformimage.width *3 + 300 ,y:100,image:platformimage }),
  new Platform({x:platformimage.width *4 + 100 ,y:350,image:platformimage }),
  new Platform({x:platformimage.width *5 + 200 ,y:500,image:platformimage }),
  new Platform({x:platformimage.width *6 + 400 ,y:300,image:splatformimage }),
  new Platform({x:platformimage.width *6 + 400,y:-49,image:createImage(flag)})
]
 genericObjects =[
  new GenericObject({x:-1,y:-1,image:createImage(background)}),
  new GenericObject({x:-1,y:-1,image:createImage(hills)}),
 
]
 scrollOffset = 0
}



let player = new Player()
let platforms = []
let genericObjects =[]
let platformimage
let splatformimage
let scrollOffset = 0
let lastKey


const keys = {
  right: { pressed: false },
  left: { pressed: false }
}









function animate() {

  requestAnimationFrame(animate)
  c.fillStyle = 'white'
  c.fillRect(0, 0, canvas.width, canvas.height)


 genericObjects.forEach(genericObject => { genericObject.draw()})

  platforms.forEach(platform => { platform.draw() })

 player.update()


  if (keys.right.pressed && player.position.x < 400 ) 
      player.velocity.x =player.speed
  else if ((keys.left.pressed && player.position.x > 100 ) ||
           (keys.left.pressed && scrollOffset === 0 && player.position.x > 0 ) ) 
       player.velocity.x = -player.speed
  else {
    player.velocity.x = 0

    if (keys.right.pressed  ) { 
      scrollOffset +=player.speed
      platforms.forEach(platform => { platform.position.x -= player.speed }) 
      genericObjects.forEach(genericObject => { genericObject.position.x -=player.speed * 0.66  })
  }
    else if (keys.left.pressed && scrollOffset > 0) { 
      scrollOffset -=player.speed
      platforms.forEach(platform => { platform.position.x += player.speed })
      genericObjects.forEach(genericObject => { genericObject.position.x += player.speed * 0.66  })
  }
  }



  platforms.forEach(platform => {
    if (player.position.y + player.height <= platform.position.y
      && player.position.y + player.height + player.velocity.y >= platform.position.y
      && player.position.x + player.width >= platform.position.x
      && player.position.x <= platform.position.x + platform.width
    ) player.velocity.y = 0
  })







  
if(keys.right.pressed && lastKey === 'right' && player.currentSprite !== player.sprites.run.right )
{
player.frames = 1
player.currentSprite = player.sprites.run.right
player.currentCropWidth =player.sprites.run.cropWidth
player.width = player.sprites.run.width
}
else if( keys.left.pressed && lastKey === 'left' && player.currentSprite !== player.sprites.run.left )
{
 
  player.currentSprite = player.sprites.run.left
  player.currentCropWidth =player.sprites.run.cropWidth
  player.width = player.sprites.run.width

}


else if( !keys.right.pressed && lastKey === 'right' && player.currentSprite !== player.sprites.stand.right )
{
  
  player.currentSprite = player.sprites.stand.right
  player.currentCropWidth =player.sprites.stand.cropWidth
  player.width = player.sprites.stand.width

}
else if( !keys.left.pressed && lastKey === 'left' && player.currentSprite !== player.sprites.stand.left )
{
  
  player.currentSprite = player.sprites.stand.left
  player.currentCropWidth =player.sprites.stand.cropWidth
  player.width = player.sprites.stand.width

}








 //win condition
 if(scrollOffset>=platformimage.width *6 + 150 && player.position.y + player.height <= platforms[6].position.y )
 {alert('YOU WIN 😃 \n 🎉🎉🎉🎉🎉🎉🎉🎉🎉🎉🎉\n🎉🎉🎉🎉🎉🎉🎉🎉🎉🎉🎉\n 🎉🎉🎉🎉🎉🎉🎉🎉🎉🎉🎉\n🎉🎉🎉🎉🎉🎉🎉🎉🎉🎉🎉'),location.reload(), console.log('You win :)'), init() }
  //lose condition
  if (player.position.y > canvas.height){alert('YOU LOSE 😧'),location.reload(), console.log('You lose :('), init() }
}



alert('USE  🢂 🢃 🢁 🢀  to play')

init()

animate()





window.addEventListener('keydown',
  ({ keyCode }) => {

    switch (keyCode) {
      case 38: console.log('up');
        player.velocity.y -= 20;
        break

      case 40: console.log('down');
        player.velocity.y += 1;
        break

      case 39: console.log('right');
        keys.right.pressed = true;
        lastKey = 'right'
        break

      case 37: console.log('left');
        keys.left.pressed = true
        lastKey = 'left'
        break
    }


  }

)
window.addEventListener('keyup',
  ({ keyCode }) => {

    switch (keyCode) {
      case 38: console.log('up');

        break

      case 40: console.log('down');

        break

      case 39: console.log('right');
        keys.right.pressed = false
       
        break

      case 37: console.log('left');
        keys.left.pressed = false
        player.currentSprite = player.sprites .stand.left
        player.currentCropWidth =player.sprites.stand.cropWidth
        player.width = player.sprites.stand.width
        break
    }


  }

)
